'use client';
import { useEffect, useState } from 'react';
import { supabase } from '../lib/supabase';
import { useAuth } from '../context/AuthContext';
import {
    Users,
    Activity,
    CheckCircle2,
    Wallet,
    Calendar,
    ArrowRight,
    Search,
    UserPlus,
    Loader2
} from 'lucide-react';
import { cn, formatCurrency } from '../lib/utils';

interface ChildProfile {
    id: string;
    full_name: string;
    email?: string;
}

interface ChildStats {
    totalExpenses: number;
    activeTasks: number;
    completionRate: number;
    mealsToday: number;
}

export default function ParentDashboard() {
    const { user } = useAuth();
    const [children, setChildren] = useState<ChildProfile[]>([]);
    const [selectedChild, setSelectedChild] = useState<string | null>(null);
    const [childStats, setChildStats] = useState<ChildStats | null>(null);
    const [loading, setLoading] = useState(true);
    const [searchEmail, setSearchEmail] = useState('');
    const [linkingChild, setLinkingChild] = useState(false);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        if (user) {
            fetchChildren();
        }
    }, [user]);

    useEffect(() => {
        if (selectedChild) {
            fetchChildStats(selectedChild);
        }
    }, [selectedChild]);

    const fetchChildren = async () => {
        try {
            const { data, error } = await supabase
                .from('family_links')
                .select(`
                    child_id,
                    profiles:child_id (id, full_name)
                `)
                .eq('parent_id', user?.id);

            if (error) throw error;

            const linkedChildren = data.map((item: any) => ({
                id: item.profiles.id,
                full_name: item.profiles.full_name
            }));

            setChildren(linkedChildren);
            if (linkedChildren.length > 0 && !selectedChild) {
                setSelectedChild(linkedChildren[0].id);
            }
        } catch (err) {
            console.error('Error fetching children:', err);
        } finally {
            setLoading(false);
        }
    };

    const fetchChildStats = async (childId: string) => {
        try {
            // Fetch expenses
            const { data: expenses } = await supabase
                .from('expenses')
                .select('amount, type')
                .eq('user_id', childId);

            const totalExp = expenses
                ?.filter(e => e.type === 'expense')
                .reduce((sum, e) => sum + Number(e.amount), 0) || 0;

            // Fetch tasks
            const { data: tasks } = await supabase
                .from('tasks')
                .select('status')
                .eq('user_id', childId);

            const activeTasks = tasks?.filter(t => t.status !== 'done').length || 0;
            const completedTasks = tasks?.filter(t => t.status === 'done').length || 0;
            const totalTasks = tasks?.length || 0;
            const completionRate = totalTasks > 0 ? Math.round((completedTasks / totalTasks) * 100) : 0;

            // Fetch meals today
            const today = new Date().toISOString().split('T')[0];
            const { count: mealsToday } = await supabase
                .from('meals')
                .select('*', { count: 'exact', head: true })
                .eq('user_id', childId)
                .gte('timestamp', `${today}T00:00:00`);

            setChildStats({
                totalExpenses: totalExp,
                activeTasks,
                completionRate,
                mealsToday: mealsToday || 0
            });
        } catch (err) {
            console.error('Error fetching child stats:', err);
        }
    };

    const handleLinkChild = async (e: React.FormEvent) => {
        e.preventDefault();
        setLinkingChild(true);
        setError(null);

        try {
            // First find the child by email in profiles
            // Since we don't have email in profiles, we might need a better way or search auth.users (restricted)
            // For now, let's assume we can search profiles by a unique field or just ask the user for the Child's User ID if they know it, 
            // but searching by full_name or email is better.
            // Let's assume we added email to profiles or we use a custom function.

            // For simplicity, let's try to find a profile with that 'full_name' or use a rpc if we had one.
            // In a real app, you'd use a more robust way to find users.
            const { data: childProfile, error: searchError } = await supabase
                .from('profiles')
                .select('id, role')
                .eq('full_name', searchEmail) // In this demo, we'll use full_name as the search key
                .eq('role', 'child')
                .single();

            if (searchError || !childProfile) {
                throw new Error('Child not found. Make sure the name is correct and they have a Child account.');
            }

            const { error: linkError } = await supabase
                .from('family_links')
                .insert([
                    { parent_id: user?.id, child_id: childProfile.id }
                ]);

            if (linkError) {
                if (linkError.code === '23505') throw new Error('You are already linked to this child.');
                throw linkError;
            }

            setSearchEmail('');
            fetchChildren();
        } catch (err: any) {
            setError(err.message);
        } finally {
            setLinkingChild(false);
        }
    };

    if (loading) {
        return (
            <div className="flex items-center justify-center min-h-[400px]">
                <Loader2 className="animate-spin text-primary" size={32} />
            </div>
        );
    }

    return (
        <div className="space-y-8 animate-in fade-in duration-500">
            <div className="flex flex-col md:flex-row md:items-center justify-between gap-4">
                <div className="flex flex-col gap-1">
                    <h2 className="text-3xl font-bold tracking-tight">Parental Dashboard</h2>
                    <p className="text-muted-foreground">Monitor and manage your children's activities.</p>
                </div>

                <form onSubmit={handleLinkChild} className="flex gap-2">
                    <div className="relative">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-muted-foreground" size={16} />
                        <input
                            type="text"
                            placeholder="Child's username..."
                            value={searchEmail}
                            onChange={(e) => setSearchEmail(e.target.value)}
                            className="pl-9 pr-4 py-2 bg-card border border-border rounded-xl focus:ring-2 focus:ring-primary outline-none transition-all w-full md:w-64"
                        />
                    </div>
                    <button
                        type="submit"
                        disabled={linkingChild || !searchEmail}
                        className="bg-primary text-primary-foreground p-2 rounded-xl hover:bg-primary/90 disabled:opacity-50 transition-colors"
                    >
                        {linkingChild ? <Loader2 className="animate-spin" size={20} /> : <UserPlus size={20} />}
                    </button>
                </form>
            </div>

            {error && (
                <div className="p-3 rounded-xl bg-destructive/10 text-destructive text-sm font-medium">
                    {error}
                </div>
            )}

            {children.length === 0 ? (
                <div className="bg-card border-2 border-dashed border-border rounded-3xl p-12 text-center">
                    <div className="w-16 h-16 bg-primary/10 text-primary rounded-2xl flex items-center justify-center mx-auto mb-4">
                        <Users size={32} />
                    </div>
                    <h3 className="text-xl font-bold mb-2">No children linked yet</h3>
                    <p className="text-muted-foreground max-w-sm mx-auto">
                        Link your child's account using their username to start monitoring their tasks, expenses, and meals.
                    </p>
                </div>
            ) : (
                <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
                    {/* Sidebar: Child List */}
                    <div className="lg:col-span-1 space-y-4">
                        <h3 className="text-sm font-bold uppercase tracking-widest text-muted-foreground px-2">Your Family</h3>
                        <div className="space-y-2">
                            {children.map((child) => (
                                <button
                                    key={child.id}
                                    onClick={() => setSelectedChild(child.id)}
                                    className={cn(
                                        "w-full flex items-center justify-between p-4 rounded-2xl border transition-all group",
                                        selectedChild === child.id
                                            ? "bg-primary border-primary text-primary-foreground shadow-lg shadow-primary/20"
                                            : "bg-card border-border hover:border-primary/50 text-card-foreground"
                                    )}
                                >
                                    <div className="flex items-center gap-3">
                                        <div className={cn(
                                            "w-10 h-10 rounded-xl flex items-center justify-center font-bold text-lg",
                                            selectedChild === child.id ? "bg-primary-foreground/20" : "bg-primary/10 text-primary"
                                        )}>
                                            {child.full_name[0].toUpperCase()}
                                        </div>
                                        <span className="font-bold">{child.full_name}</span>
                                    </div>
                                    <ArrowRight size={18} className={cn(
                                        "transition-transform group-hover:translate-x-1",
                                        selectedChild === child.id ? "opacity-100" : "opacity-0"
                                    )} />
                                </button>
                            ))}
                        </div>
                    </div>

                    {/* Main Content: Child Stats */}
                    <div className="lg:col-span-3 space-y-8">
                        {selectedChild && childStats && (
                            <>
                                <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
                                    <StatCard
                                        title="Total Spent"
                                        value={formatCurrency(childStats.totalExpenses)}
                                        icon={Wallet}
                                        color="text-red-500 bg-red-500/10"
                                    />
                                    <StatCard
                                        title="Active Tasks"
                                        value={childStats.activeTasks.toString()}
                                        icon={CheckCircle2}
                                        color="text-blue-500 bg-blue-500/10"
                                    />
                                    <StatCard
                                        title="Completion"
                                        value={`${childStats.completionRate}%`}
                                        icon={Activity}
                                        color="text-emerald-500 bg-emerald-500/10"
                                    />
                                    <StatCard
                                        title="Meals Today"
                                        value={childStats.mealsToday.toString()}
                                        icon={Calendar}
                                        color="text-amber-500 bg-amber-500/10"
                                    />
                                </div>

                                <div className="bg-card rounded-3xl border border-border p-8 text-center">
                                    <h3 className="text-xl font-bold mb-4">Activity Log (Coming Soon)</h3>
                                    <p className="text-muted-foreground">
                                        Detailed activity logging and reports for {children.find(c => c.id === selectedChild)?.full_name} will be available here.
                                    </p>
                                </div>
                            </>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}

function StatCard({ title, value, icon: Icon, color }: any) {
    return (
        <div className="bg-card p-6 rounded-3xl border border-border shadow-sm flex flex-col gap-4">
            <div className={cn("w-12 h-12 rounded-2xl flex items-center justify-center", color)}>
                <Icon size={24} />
            </div>
            <div>
                <p className="text-xs text-muted-foreground font-bold uppercase tracking-widest mb-1">{title}</p>
                <h4 className="text-2xl font-black tracking-tight">{value}</h4>
            </div>
        </div>
    );
}
